// @ts-check
import figlet from 'figlet'

import { prompt } from 'prompts'
// import chalk from 'chalk'
import * as kolorist from 'kolorist'

// import ora from 'ora';

// console.log(chalk.cyan('aaa'))


import { execSync } from 'child_process'

async function init() {
  async function banner() {
    figlet.text('METACLOUD ', {
      font: 'Standard',
      horizontalLayout: 'default',
      verticalLayout: 'default',
      width: 80,
      whitespaceBreak: true
    },
      (_, data) => {
        console.log(data);
      }
    )
  }

  async function question() {
    let questions = [
      {
        type: "select",
        name: "localOrRemote",
        message: 'Develop locally or remotely',
        choices: [
          { title: 'locally(recommended)', value: 'locally' },
          { title: 'remotely', value: 'remotely' }
        ],
        initial: 0,
      },
      {
        type: "select",
        name: "dindOrDfromD",
        message: 'DinD(docker in docker) or DfromD(docker from docker)',
        choices: [
          { title: 'DinD(recommended)', value: 'DinD' },
          { title: 'DfromD', value: 'DfromD' }
        ],
        initial: 0,
      },
      {
        type: "select",
        name: "devRole",
        message: 'frontend or fullstack or both+devops',
        choices: [
          { title: 'frontend', value: 'frontend' },
          { title: 'fullstack', value: 'fullstack' },
          { title: 'both+devops(recommended)', value: 'devops' }
        ],
        initial: 0,
      },

    ]
    let res = await prompt(questions)
    return res
  }


  await banner()
  let result = await question()

  console.log(result)
  const {
    localOrRemote,
    dindOrDfromD,
    devRole
  } = result

  console.log(`\nScaffolding project ...`)

  let a = execSync(`rm -rf MetaCloud`)
  console.log(a)
  let b = execSync('git clone https://github.com/oldwinter/metacloud.git')
  console.log(b)
  // const spinner = ora('Loading unicorns').start();

  // setTimeout(() => {
  //   spinner.color = 'yellow';
  //   spinner.text = 'Loading rainbows';
  // }, 1000);

  console.log(`\nDone. Now run:\n`)
  if (devRole === "frontend"){
    console.log(`${kolorist.green('cd metacloud')}`)
    console.log(`${kolorist.green('npm install')}`)
    console.log(`${kolorist.green('npm run dev')}`)
  }

  console.log()

}

init().catch((e) => {
  console.error(e)
})